import React, { useState, useEffect } from 'react'

// components
import SearchField from "./SearchField"
import SearchResult from "./SearchResult"
import AddContactForm from "./AddContactForm"
import ContactList from "./ContactList"
import contactsService from "./services/contacts";
import Notification from "./Notification"

var showSearchResults=false;

const App = () => {
  const [ persons, setPersons ] = useState([]) 
  const [ newName, setNewName ] = useState('');
  const [ newNumber, setNewNumber] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [searchName, setSearchName] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  const [notification, setNotification] = useState(null);

  // useEffect(() => {
  //   console.log('useEffect called')
  //   axios
  //     .get('http://localhost:3001/persons')
  //     .then(response => {
  //       console.log('promise fulfilled')
  //       setPersons(response.data)
  //     })
  // }, [])

  useEffect(() => {
    console.log("service useEffect called")
    contactsService.getAll().then(initialContacts => {
      console.log("initial contacts: ", initialContacts)
      setPersons(initialContacts)   
    })
  }, []);



  // getFilteredContacts = () => persons.filter(contact => contact.name.includes(this.state.searchName)
  // this.getFilteredContacts().map(contact =>

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  };

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  };

  const handleSearchChange = (event) =>{
    console.log("searching for: ", event.target.value)
    persons.map(person => console.log("persons:", person.name))
    var searchMatch = persons.filter(person => person.name.includes(event.target.value))
    console.log("search match is: ", searchMatch)
    if(event.target.value ===''){
      showSearchResults = false;
      setSearchName('');
      console.log("search is empty")
    }else{
    console.log("searchName update: ", event.target.value)
    setSearchName(event.target.value);
    var matches = persons.filter(person => person.name.toLowerCase().includes(searchName.toLowerCase()));
    if (matches.length === 0){
      setSearchResults([{name:"No matches", number:""}])
    }else{
      setSearchResults(matches);
      showSearchResults=true;
    }
    }
  };



  const checkforName = (name) =>{
    console.log("checking for ", name.name);
    var duplicate = false;
    for(let i=0; i<persons.length; i++){
      console.log("contact ", persons[i].name, "newName", name.name)
      if(persons[i].name.toLowerCase() === name.name.toLowerCase()){
        duplicate = true;
        break
      }
    }
    return duplicate;
  };

  const rejectName = (name) => {
    console.log(name.name, " rejected")
    setNotification(`${name.name.toLowerCase()}'s entry was left alone`)
          setTimeout(() => {
            setNotification(null)}, 3000)
    setNewName('')
  };

  const handleSubmit = (event) =>{
    event.preventDefault();
    var newNameObject = {name:newName}
    var newNumberObject = {number:newNumber}
    console.log("-Submission-  newNameObject: ", newNameObject, " newNumberObject", newNumberObject)

    if(newName=== '' || newNumber.length !== 10){
      alert("Please enter a valid name and number first")
      setNewName('')
      setNewNumber('')
    }else{
      var updateNum = false;
      if (checkforName(newNameObject)){
        if(window.confirm(`${newNameObject.name.toLowerCase()} already exists! Do you want to update ${newNameObject.name.toLowerCase()}'s phone number?`)){
          updatePhoneNumber(newNameObject, newNumberObject)
        }else{
          rejectName(newNameObject)
        }
      }else{
        let personIds = persons.map(person => parseInt(person.id));
        let maxIdNum = Math.max(...personIds);
        let newPersonId = String(maxIdNum+1)
        let newPerson = {name: newName, number: newNumber, id: newPersonId};
        console.log("Submit registered", "newName: ", newNameObject, "newNumber: ", newNumber, "maxIdNum: ", maxIdNum, "MaxIdNum+1", maxIdNum + 1);
        console.log("Person built: ", newPerson, " ID: ", newPerson.id, " Name: ", newPerson.name, " Number: ", newPerson.number); 
        contactsService.create(newPerson).then(returnedPerson =>{
          setPersons(persons.concat(returnedPerson))
          setNotification(`${newName} added!`)
          setTimeout(() => {
            setNotification(null)}, 3000)
          setNewName('');
          setNewNumber('');
          })
      }
    }
  };


  const updatePhoneNumber = (contactName, contactNumber) =>{
    console.log("-UpdatePhoneNumberProps-", contactName, "peops name", contactName.name, "props number", contactNumber.number)
    var contactToUpdate = persons.filter(person => person.name === String(contactName.name));
    if(contactToUpdate[0]){
      console.log("Found a contactToUpdate: ", contactToUpdate[0], contactToUpdate[0].name, contactToUpdate[0].number, contactToUpdate[0].id)
      var updatedContact = {name: contactName.name, number: contactNumber.number}
      contactsService.update(contactToUpdate[0].id, updatedContact).then(returnedPerson =>{
        var oldPersons = persons.filter(person => person.id !== returnedPerson.id)
        setPersons(oldPersons.concat(returnedPerson))
        setNewName('');
        setNewNumber('');
      })
    }
  };
  
  const deleteEntry = (id) =>{
    console.log('Delete called on id: ', id);
    console.log("ID", id, "'s name is: ", persons.filter(person => person.id === id)[0].name)
    var personToDelete = persons.filter(person => person.id === id)[0]
    console.log("personToDelete: ", personToDelete)
    if(window.confirm(`Are you sure you want to delete ${personToDelete.name}`)){
      contactsService.deleteEntry(id);
      setPersons(persons.filter(person => person.id !== id));
      setSearchResults([])
      setNotification(`${personToDelete.name} removed!`)
          setTimeout(() => {
            setNotification(null)}, 3000)
    }else{
      window.alert("OK. No contacts deleted.")}
    
  };




  return (
    <div>
      {notification != null ? <Notification msg={notification} /> : <h3></h3> }
      <h2>Phonebook</h2>
      {/* <div>
        Search: <input onChange={handleSearchChange} value={searchName}/>
        <h3>Searching for: {searchName}</h3>
      </div> */}

      <SearchField handleChange={handleSearchChange} searchName={searchName}/>


      <AddContactForm 
        handleContactSubmit={handleSubmit} 
        handleNameFieldChange={handleNameChange}
        nameValue={newName}
        handleNumberFieldChange={handleNumberChange}
        numberValue={newNumber}
      />

      {/* <form onSubmit={handleSubmit}>
        <div>
          name: <input onChange={handleNameChange} value={newName} type="tel"/>
        </div>
        <div>
          number: <input onChange={handleNumberChange} value={newNumber}/>
        </div>
        <div>
          <button type="submit" >add</button>
        </div>
      </form> */}

      <div>newName is currently: {newName}</div>

      <h2>Contacts</h2>
      <button onClick={() => setShowAll(!showAll)}>Show/Hide Phonebook</button>

      {showAll === true ? 
      <ContactList contacts={persons} handleDelete={deleteEntry}/> :
      <div> <h3>Contacts Hidden</h3> </div>}
      
      <h2>Search Results</h2>
        {showSearchResults === true ? 
        searchResults.map(searchResult =>  
        <SearchResult searchResult={searchResult} />):
        <div></div> }
      

    </div>
  )
}

export default App