const ContactList = (props) => {
    // console.log("ContactList props: ", props);
    // console.log("ContactList contacts", props.contacts);
    
    // props.contacts.map(contact => console.log('Map: ',contact.name, ' ', contact.number))

    const handleDelete = (id) =>{
        console.log("ContactList handleDelete called on ID: ", id)
        props.handleDelete(id);
    }
    const deleteButtonStyle = {
                                // textAlign: "left",
                                // position: "absolute",
                                // right:"50%",
                                // width:"10%",
                                // borderRadius:"5px"
                                // float: "right",
};

    return(
        <div>
            {props.contacts.map((contact) => {
                return (<div> <p key={contact.id}>ID: {contact.id} {contact.name} {contact.number}<button style={deleteButtonStyle} onClick={() => handleDelete(contact.id)}>Delete<br></br>{contact.name.split(' ')[0]}?</button></p></div>)} )}</div>
    )
}

export default ContactList;